                <p class="header_center_name">{{data.author}}</p>
                <div class="header_center_label">
                  <div class="label_grade">
                    <img
                      class="label_grade_img"
                      :class="{'opacity_0':data.is_carvip != 1 }"
                      v-if="data.is_carvip == 1"
                      :src="data.is_carvip == 1 ? icon_iscarvip : ''"
                      alt
                    />
                    <span class="label_grade_name">{{data.level}}</span>
                  </div>
                  <div class="label_heat">
                    <img class="label_heat_img" :src="icon_label_d" alt />
                    <span class="label_grade_name">{{data.extcredits2}}°</span>
                  </div>
                </div>
              </div>
            </div>
            <div class="news flex justify-end">
              <div class="usage_news" @click="addZan(data,1)">
                <img
                  class="usage_news_img"
                  v-if="data.is_zan == 1"
                  src="../../assets/images/icon_love_selection.png"
                  alt
                />
                <img
                  class="usage_news_img"
                  v-else
                  src="../../assets/images/icon_love_normal.png"
                  alt
                />
                <span>{{data.zan}}</span>
              </div>
              <div class="usage_news usage_news_right" @click="addZan(data,2)">
                <img
                  class="usage_news_img"
                  v-if="data.is_zan == 2"
                  src="../../assets/images/icon_stepon_selected.png"
                  alt
                />
                <img
                  class="usage_news_img"
                  v-else
                  src="../../assets/images/icon_stepon_normal.png"
                  alt
                />
                <span>{{data.cai}}</span>
              </div>
            </div>
          </div>
          <div class="content" ref="content">
            <p class="text">
              <span class="name" @click="handleToZdList(data)">{{data.class_name}}</span>
              {{data.message}}
            </p>
            <div v-if="data.attachment == 2">
              <div class="image-box" v-if="data.image_list  ? data.image_list.length : false">
                <div v-if="data.image_list.length == 4">
                  <div>
                    <img
                      class="img_content"
                      :src="data.image_list[0]"
                      @click="ImagePreview(data.image_list,0)"
                    />
                    <img
                      class="img_content"
                      :src="data.image_list[1]"
                      @click="ImagePreview(data.image_list,1)"
                    />
                  </div>
                  <div>
                    <img
                      class="img_content"
                      :src="data.image_list[2]"
                      @click="ImagePreview(data.image_list,2)"
                    />
                    <img
                      class="img_content"
                      :src="data.image_list[3]"
                      @click="ImagePreview(data.image_list,3)"
                    />
                  </div>
                </div>
                <div v-else class="image_content">
                  <img
                    v-for="(item, index) in data.image_list"
                    :key="index"
                    class="img_content"
                    :class="{'img_content_1':data.image_list.length == 1}"
                    :src="item"
                    alt
                    @click="ImagePreview(data.image_list,index)"
                  />
                </div>
              </div>
            </div>
            <div v-if="data.attachment == 1">
              <div
                class="video"
                v-bind:class="{'video_1':data.video_heng_shu == 1,'video_2':data.video_heng_shu == 0}"
                v-if="data.attachment == 1"
                @click="handlePlay('video',data)"
              >
                <video ref="video" :src="data.video" 
                playsinline="true" x-webkit-airplay="true" x5-playsinline="true" webkit-playsinline="true" x5-video-player-type="h5"
                preload="auto" :poster="data.image_list[0]"></video>
                <img class="icon-play" src="../../assets/images/icon_play.png" />
              </div>
            </div>
            <div class="subinfo flex justify-space-between">
              <p>{{data.address}}</p>
              <span>{{data.dateline|timeformat()}}</span>
            </div>
          </div>

          <div class="comment" ref="comment">
            <p class="line"></p>
            <img
              v-if="this.pageInfo.count == 0"
              style="width:100%"
              src="../../assets/images/pic_comment.png"
            />
            <p class="point" v-if="this.pageInfo.count == 0">快给楼主写评论吧</p>
            <p class="count" v-if="this.pageInfo.count != 0">全部评论({{this.pageInfo.count}})</p>
            <van-list v-model="loading" :finished="finished" finished-text @load="onLoad">
              <ul class="list">
                <li v-for="(item,key) in commentData" :key="key">
                  <div class="head flex justify-space-between">
                    <div class="flex">
                      <div class="avatar-box" @click="handleToMyPage(item)">
                        <van-image
                            round
                            class="portrait_img"
                            :src="item.avatar"
                            loading-icon="https://testapi.e-power.vip/get_image.php?type=avatar&file_url=data/avatar/000/00/18/52_avatar_big.jpg"
                          />
                        <!-- <img :src="item.avatar" class="portrait_img" alt /> -->
                        <img
                          class="isvip"
                          src="../../assets/images/icon_isvip.png"
                          v-if="item.is_ident_icon == 1"
                        />
                      </div>
                      <div class="header_center">
                        <p class="header_center_name">{{item.author}}</p>
                        <div class="header_center_label">
                          <div class="label_grade">
                            <img
                              class="label_grade_img"
                              :class="{'opacity_0':data.is_carvip != 1 }"
                              v-if="data.is_carvip == 1"
                              :src="data.is_carvip == 1 ? icon_iscarvip : ''"
                              alt
                            />
                            <span class="label_grade_name">{{item.level}}</span>
                          </div>
                          <div class="label_heat">
                            <img class="label_heat_img" :src="icon_label_d" alt />
                            <span class="label_grade_name">{{item.extcredits2}}°</span>
                          </div>
                        </div>
                      </div>
                    </div>
                    <p class="time flex align-items-center">{{item.dateline|timeformat()}}</p>
                  </div>
                  <div class="text" @click="replyText(item.pid,item.uppid)"><span v-if="item.reply_author && item.reply_author != ''" class="text_span">回复{{item.reply_author}}：</span> {{item.message}}</div>
                </li>
              </ul>
            </van-list>
          </div>
        </div>
      </div>
    </div>
    <div class="input-box" v-if="share !=1 ">
      <p class="line"></p>
      <div>
        <van-cell-group>
          <van-field
            @focus="animateWidth('company_name','blur')"
            v-model="postParameter.message"
            placeholder="我也来说～"
            :border="false"
            ref='inputs'
            @keyup.enter="addPost"
          >
            <span slot="button" style="color: #424E77;" @click="addPost">发布</span>
          </van-field>
        </van-cell-group>
      </div>
    </div>
  </div>
</template>

<script>
import { getFromNom } from "@js/moment";
import { ImagePreview, Toast } from "vant";
export default {
  data() {
    return {
      share: this.$route.query.share, // 1: 是从分享页 打开
      loading: false,
      finished: false,
      icon_label_d: require("../../assets/images/icon_label.png"),
      icon_iscarvip: require("../../assets/images/icon_iscarvip.png"),
      data: null,
      commentData: [],
      pageInfo: {
        page: 0,
        size: 10,
        count: 0
      },
      postParameter: {
        token: this.$store.state.userData,
        tid: this.$route.query.tid,
        message: "",
        uppid: "",
        reply_pid: "0",
        aid_list: "",
        attachment: 0
      },
      items: {
        token: this.$store.state.userData,
        tid: this.$route.query.tid,
        uppid:"",
        reply_pid:'',
        message: "",
        aid_list: "",
        attachment: 0
      }
    };
  },
  created() {
    document.title = " ";
    if (this.$route.query.share == 1) {
      document.title = "E区新能源车主壹社区";
    }
    this.getSquaresItemDetail();
    this.getSquaresDetailComment();
  },
  methods: {
    onLoad() {
      if (
        this.pageInfo.page ==
        Math.ceil(this.pageInfo.count / this.pageInfo.size) - 1
      ) {
        this.finished = true;
      }
      if (this.commentData.length) {
        this.pageInfo.page++;
        this.getSquaresDetailComment();
      } else {
      }
    },
    // 跳转主题页
    handleToZdList(item) {
      let url = `${this.$store.state.indexUrl +
          "specialtopic/list?typeid=" +
          item.typeid +
          "&" +
          this.$store.state.appParams}`,
        json = { url: url, share: "0", keyboard: "0" };
      console.log(`teldapp://newWebview?params=` + JSON.stringify(json));
      window.location.href =
        `teldapp://newWebview?params=` + JSON.stringify(json);
    },
    // 视频播放
    handlePlay(ref) {
      let url = `${this.$store.state.indexUrl +
          "square/playVideo?" +
          // this.$route.query.tid +
          // "&" +
          this.$store.state.appParams}`,
        json = { url: url, share: "0", keyboard: "0" };
      console.log(url);
      window.location.href =  `teldapp://newWebview?params=` + JSON.stringify(json);
     // this.$router.push({path:'/square/playVideo',query:{tid:this.$route.query.tid}})
      return null;
      let obj = this.$refs[ref];
      obj.play();

      obj.webkitRequestFullScreen();
      obj.addEventListener("pause", () => {
        obj.controls = false;
      });
    },
    // 跳转我的页面
    handleToMyPage(item) {
      if (!this.$store.state.userData) {
        //  window.location.href= "teldapp://login"
        let frame = document.createElement("frame");
        frame.src = "teldapp://login";
        document.documentElement.childNodes[0].appendChild(frame);
        return null;
      }
      if (item.is_self == 1) {
        this.$router.push({ path: "/my/index/square" });
      } else {
        this.$router.push({
          path: "/my/index/square",
          query: { uid: item.authorid }
        });
      }
      //this.$router.push({path:'/my/index/square',query:{uid:''}})
    },
    // 输入框获取焦点
    animateWidth() {
      setTimeout(() => {
        if (
          this.$refs.comment.clientHeight + this.$refs.content.clientHeight >
          600
        ) {
          window.scrollTo(0, document.body.scrollHeight);
        } else {
          window.scrollTo(0, 0);
        }
      }, 0);
    },
    //回复
    replyText(pid,uppid) {
      this.items.uppid = uppid;
      this.items.reply_pid=pid
      this.$refs.inputs.focus()
    },
    // 图片预览
    ImagePreview(v, key) {
      ImagePreview({
        images: v,
        startPosition: key,
        loop: false
      });
    },
    getSquaresDetailComment() {
      this.api
        .getSquaresDetailComment({
          token: this.$store.state.userData,
          tid: this.$route.query.tid,
          page_index: this.pageInfo.page,
          page_size: this.pageInfo.size
        })
        .then(res => {
          this.loading = false;
          if (res.err_code == 0) {
            if (this.pageInfo.page == 0) {
              this.commentData = [];
            }
            if (
              !res.data.post.length ||
              res.data.post.length < this.pageInfo.size
            ) {
              this.finished = true;
            }
            
            this.commentData = [...this.commentData, ...res.data.post];
            this.pageInfo.count = res.data.total;
            if (this.pageInfo.page != 0) {
              window.scrollTo(0, document.body.scrollHeight);
            }
          }
        });
    },
    // 点赞或踩
    addZan(item, type) {
      if (this.share == 1) {
        return null;
      }
      if (!this.$store.state.userData) {
        //  window.location.href= "teldapp://login"
        let frame = document.createElement("frame");
        frame.src = "teldapp://login";
        document.documentElement.childNodes[0].appendChild(frame);
        return null;
      }
      let tid = item.tid,
        token = this.$store.state.userData;
      Toast.loading({
        message: "加载中...",
        forbidClick: true
      });
      this.api.addZan({ token, tid, type }).then(res => {
        Toast.clear();
        if (res.err_code == 0) {
          if (item.is_zan == 1) {
            if (type == 1) {
              item.zan = parseInt(item.zan) - 1;
              item.is_zan = 0;
            } else if (type == 2) {
              item.is_zan = type;
              item.zan = parseInt(item.zan) - 1;
              item.cai = parseInt(item.cai) + 1;
            }
          } else if (item.is_zan == 2) {
            if (type == 1) {
              item.is_zan = type;
              item.zan = parseInt(item.zan) + 1;
              item.cai = parseInt(item.cai) - 1;
            } else if (type == 2) {
              item.is_zan = 0;
              item.cai = parseInt(item.cai) - 1;
            }
          } else {
            if (type == 1) {
              item.zan = parseInt(item.zan) + 1;
            } else if (type == 2) {
              item.cai = parseInt(item.cai) + 1;
            }
            item.is_zan = type;
          }
          if (item.is_zan) {
          }

          // Toast(res.data.msg)
        } else {
          Toast(res.err_msg);
        }
      });
    },
    // 回复
    addPost() {
      if (!this.$store.state.userData) {
        //    window.location.href= "teldapp://login"
        let frame = document.createElement("frame");
        frame.src = "teldapp://login";
        document.documentElement.childNodes[0].appendChild(frame);
        return null;
      }
      if (!this.postParameter.message) {
        return Toast("输入不能为空");
      }
      Toast.loading({
        message: "加载中...",
        forbidClick: true
      });
      if (this.items.reply_pid == "") {
        this.api.addPost(this.postParameter).then(res => {
          Toast.clear();
          if (res.err_code == 0) {
            Toast("发布成功");
            this.postParameter.message = "";
            this.pageInfo.page = 0;
            this.getSquaresItemDetail();
            this.getSquaresDetailComment();
          }
        });
      } else {
        this.items.message = this.postParameter.message;
        this.api.addPost(this.items).then(res => {
          Toast.clear();
          if (res.err_code == 0) {
            this.$toast("回答成功！");
            this.postParameter.message = "";
            this.items.pid = '';
            this.getSquaresItemDetail();
            this.getSquaresDetailComment();
          }
        });
      }
    },
    getSquaresItemDetail() {
      this.api
        .getSquaresItemDetail({
          token: this.$store.state.userData,
          tid: this.$route.query.tid
        })
        .then(res => {
          if (res.err_code == 0) {
            this.data = res.data.thread;
          }
        });
    },
    onClickLeft() {
      this.$router.go(-1);
    },
    onClickRight() {
      let img = this.data.image_list.length
        ? this.data.image_list[0]
        : this.data.avatar;
      let url = `${this.$store.state.indexUrl +
          "square/details?share=1" +
          "&" +
          this.$store.state.appParams}`,
        json = {
          url: url,
          channel: "wxp,wxf,sina,qqp,qqz",
          title: `【E区】${this.data.message}`,
          desc: this.data.message,
          image: img
        };
      console.log(url);
      window.location.href = `teldapp://share?params=` + JSON.stringify(json);
    }
  },
  filters: {
    timeformat(v) {
      if (v) {
        return getFromNom(v * 1000);
      } else {
        return "";
      }
    }
  }
};
</script>
